import React from 'react';
import { Helmet } from 'react-helmet-async';
const Meta = ({ title, description }) => {
	return (
		<Helmet>
			<title>{title}</title>
			<meta name="description" content={description} />
		</Helmet>
	);
};

Meta.defaultProps = {
	title: '   ',
	description:
		'                                                    '
};

export default Meta;
